import { betweenOrEqual } from "../../../helpmodule.js";
"use strict";

const testInputArr: string[] = getTESTInput();
const inputArr: string[] = getInput();

const VALIDS = `byr (Birth Year)
iyr (Issue Year)
eyr (Expiration Year)
hgt (Height)
hcl (Hair Color)
ecl (Eye Color)
pid (Passport ID)
cid (Country ID)`.split("\n").map((line: string) => line.trim().split(" ")[0])

const VALIDSFORA = VALIDS.filter((str: string) => str !== "cid")

// type Passport = {
//     byr: number,
//     iyr: number,
//     eyr: number,
//     hgt: string,
//     hcl: string,
//     ecl: string,
//     pid: string,
//     cid?: string
// }


const checkIfValid = (str: string, minimumValidKeys: string[]) => {
    const passportObj: object = str
        .split(/\s/)
        .reduce((curr: any, next: string): object =>
            (curr[next.split(":")[0].toString()] =
                next.split(":")[1], curr), {})
    return minimumValidKeys.findIndex((key: string) => !Object.keys(passportObj).includes(key)) === -1


}




log("test a:",

    testInputArr.filter((line: string) => checkIfValid(line, VALIDSFORA))

)

log("a:",
    inputArr.filter((line: string) => checkIfValid(line, VALIDSFORA))
        .length
)


log("test b:",


)

log("b:",

)


function log(...args: any[]): void {
    if (typeof (console) !== 'undefined') {
        console.log(...args);
        document.querySelector('pre')!.innerHTML += "<br/>" + args.join(" ");
    }
}

function handleInputStr(str: string) {
    return str.split(/\n\s+\n/).map(e => e.trim().replace(/\n\s+/g, " "))
}


